<template>
  <div class="registPage">
    <div class="registBack">
      <!-- el -->
      <h1 class="registTitle"></h1>
      <el-form
      size="small"
        ref="registForm"
        :rules="rules"
        :model="registForm"
        class="registCotainer"
        label-width="80px"
      >
        <el-form-item label="" prop="userName">
          <el-input
            v-model="registForm.userName"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0'" prop="password">
          <el-input
            type="password"
            v-model="registForm.password"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item label="" prop="realName">
          <el-input
            v-model="registForm.realName"
            placeholder=""
          ></el-input>
        </el-form-item>

        <!-- <el-form-item label="" prop="version">
          <el-input
          type="number"
            v-model="registForm.version"
            placeholder=""
          ></el-input>
        </el-form-item> -->

        <el-form-item :label="'\xa0\xa0\xa0'" prop="sex">
          <el-radio-group v-model="registForm.sex">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0'" prop="birth">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              v-model="registForm.birth"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="mobile">
          <el-input
            v-model="registForm.mobile"
            placeholder=""
          ></el-input>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0'" prop="email">
          <el-input
            v-model="registForm.email"
            placeholder=""
          ></el-input>
        </el-form-item>

        <!--  <el-form-item label="" prop="">
          <el-input type="file" v-model="registForm.realName"></el-input>
        </el-form-item> -->

        <el-form-item label="">
          <el-select style="width: 420px;"  placeholder="" v-model="registForm.deptId">
            <el-option v-for="(dept,index) in deptInfo" :key="dept.deptId" :label="dept.deptName" :value="dept.deptId">{{ dept.deptName }}</el-option>
            <!-- <el-option label="" value=""></el-option>s -->
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-select style="width: 420px;" multiple placeholder="" v-model="registForm.deptId">
            <el-option v-for="(dept,index) in deptInfo" :key="dept.deptId" :label="dept.deptName" :value="dept.deptId">{{ dept.deptName }}</el-option>
            <!-- <el-option label="" value=""></el-option>s -->
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <input  type="file" name="uploadFile" multiple @change="handlerClick"/>
       
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="userRegist"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
let _fileObj;
import axios from 'axios';
import { mapGetters } from 'vuex';
export default {
  name: "regist",
  data() {
    return {
      registForm: {
        userName: "",
        password: "",
        deptId:"",
        realName: "",
        birth: "",
        sex:'',
        mobile: "",
        email: "",
        image:''
        // version:1,
      },
      files:[],
      // :
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
        realName: [
          { required: true, message: "", trigger: "blur" },
        ],
        birth: [{ required: true, message: "", trigger: "blur" }],
        sex: [{ required: true, message: "", trigger: "blur" }],
        // deptName: [{ required: true, message: "", trigger: "blur" }],
        mobile: [
          { required: true, message: "", trigger: "blur" },
        ],
        email: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  mounted(){
    // ,
    this.$store.dispatch("selectAllDepts",{})
  },
  computed: {
    ...mapGetters(['deptInfo'])
  },
  methods: {
    // 
   async userRegist() {
    try {
      // 
      const {birth,deptId,email,mobile,password,realName,sex,userName} = this;
      await this.$store.dispatch("userRegist", {
        birth:this.registForm.birth,
        deptId:this.registForm.deptId,
        email:this.registForm.email,
        mobile:this.registForm.mobile,
        password:this.registForm.password,
        realName:this.registForm.realName,
        sex:this.registForm.sex,
        userName:this.registForm.userName,
        image:this.registForm.image
      });
      this.$router.push('/login')
    } catch (error) {
      alert(error.msg)
    } 
    
    },

    // 
    async handlerClick(e){
      let uploadFile = e.target.files[0];
        // 
        // this.files = Array.from(uploadFile)
         _fileObj = uploadFile;
         let formData = new FormData();
      formData.append('uploadFile',_fileObj)
      const result =  await axios.post("http://localhost:8080/file/upload/image",formData)
      this.registForm.image=result.data.data
    },
   

    
  },
};
</script>

<style lang="less" scoped>
.registPage {
  width: 100%;
  height: 100%;
  /* background-size: contain; */
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../../assets/ai.png);

  /* background: url(../../assets/loginBack.png); */
  position: relative;

  .registBack {
    width: 500px;

    border-radius: 15px;
    background-clip: border-box;
    background-color: aliceblue;
    border: 1px solid #eaeaea;
    margin: auto;
    padding: 30px;
    box-shadow: 0 0 25px #a9b2b6;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .registTitle {
      text-align: center;
      margin: 0px 5px 20px 5px;
    }
  }
  .el-form-item {
    margin-bottom: 15px;
  }
}
</style>